import React, { FC } from 'react';
import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  IconButton,
  List,
  ListItem,
  styled,
  Typography
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';

interface IDialogTitle {
  children?: React.ReactNode;
  onClose: () => void;
}

const whoCannotBeADonorLong = [
  'Nie mog oddawa krwi osoby, ktre choroway na wirusowe zapalenie wtroby (tzw. taczk zakan), nosiciele wirusa HIV oraz chorzy na AIDS. Krwi nie pobiera si od narkomanw, od osb majcych kontakty seksualne z wieloma partnerami lub partnerkami, szczeglnie znanymi od niedawna.',

  'Dawcami krwi nie powinni by osoby z aktywn lub przebyt powan chorob ukadu krenia, ukadu nerwowego, ukadu pokarmowego, ukadu oddechowego, nerek, skry (np. uszczyca), ukadu immunologicznego, ukadu endokrynnego (np. cukrzyca, choroby tarczycy), tkanki cznej. ',

  'Nie mona oddawa krwi zaywajc leki (poza antykoncepcj hormonaln i niektrymi lekami na nadcinienie).',

  'Dyskwalifikacj sta objci s kandydaci z chorobami krwi i ukadu krwiotwrczego, skonnoci do patologicznych krwawie, z nawracajcymi omdleniami i napadami drgawkowymi, chorobami nowotworowymi, chorobami tropikalnymi, chorobami psychicznymi, chorob alkoholow.',

  'Nie kwalifikuj si na krwiodawcw osoby leczone ludzkim hormonem wzrostu, po przeszczepie rogwki, posiadajce w rodzinie chorego na chorob Creutzfeldta-Jakoba oraz przebywajce powyej p roku w latach 01.01.1980  31.12.1996 w Wielkiej Brytanii, Francji i Irlandii.',

  'Dyskwalifikacj czasow obejmuje si midzy innymi osoby, ktre miay kontakt z zakanie chorym lub przebyy ostr chorob zakan, po szczepieniach ochronnych, w ciy, po porodzie, w trakcie miesiczki i okresie okoomiesiczkowym, po leczeniu stomatologicznym.',

  'Nie mona oddawa krwi w cigu 6 miesicy po zabiegu operacyjnym, po przetoczeniu krwi, po wykonaniu tatuau, przekuciu rnych czci ciaa, akupunkturze, depilacji kosmetycznej, zabiegach diagnostycznych (np. gastroskopii), a take po pobycie w zakadzie karnym. Twoja krew zostanie zbadana, aby stwierdzi, czy nie jeste zakaony/a ki, HIV, taczk zakan B lub C. Jeli test wypadnie dodatnio, nie zostanie przetoczona. Jednak przy kadej infekcji pomidzy momentem zakaenia i chwil, gdy staje si moliwe wykrycie go drog bada laboratoryjnych, upywa pewien czas. W tym okresie w adnym przypadku nie wolno oddawa krwi, poniewa moe by rdem zakaenia, chocia testy laboratoryjne s jeszcze ujemne. Nie oddawaj wic krwi, jeeli przez ryzykowne kontakty lub zachowania narazie/a si na niebezpieczestwo.',

  'Po pobycie w krajach, w ktrych wystpuj ogniska endemiczne chorb tropikalnych  aktualna lista znajduje si na stronie www w zakadce Aktualnoci Aktualna sytuacja epidemiologiczna.'
];

const CustomDialogTitle: FC<IDialogTitle> = ({
  children,
  onClose,
  ...props
}) => {
  return (
    <DialogTitle {...props} sx={{ m: 0, p: 2 }}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500]
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
};

const CannotBeADonorDialog = () => {
  const [open, setOpen] = React.useState(false);
  const handleClickOpenDialog = () => {
    setOpen(true);
  };
  const handleOnCloseDialog = () => {
    setOpen(false);
  };
  return (
    <div>
      <Button variant="outlined" onClick={handleClickOpenDialog}>
        Czytaj wicej
      </Button>
      <Dialog
        onClose={handleOnCloseDialog}
        aria-labelledby="customized-dialog-title"
        open={open}
      >
        <CustomDialogContent handleOnCloseDialog={handleOnCloseDialog} />
      </Dialog>
    </div>
  );
};
export default CannotBeADonorDialog;

const CustomDialogContent: FC<{
  handleOnCloseDialog: () => void;
}> = ({ handleOnCloseDialog }) => {
  return (
    <>
      <CustomDialogTitle onClose={handleOnCloseDialog}>
        Regulamin - kto nie moe odda krew?
      </CustomDialogTitle>
      <DialogContent dividers>
        <List
          sx={{
            ml: 4,
            fontFamily: 'Poppins',
            listStyleType: 'decimal',
            '& .MuiListItem-root': {
              display: 'list-item'
            }
          }}
        >
          {whoCannotBeADonorLong.map((infoText) => (
            <ListItem>
              <Typography gutterBottom>{infoText}</Typography>
            </ListItem>
          ))}
        </List>
      </DialogContent>
    </>
  );
};
